import { Button, Col, Input } from 'antd'
import React, { useState } from 'react'
import { getDatabase, push, ref, set } from 'firebase/database'

const AddMedicineView = () => {
    const [name, setName] = useState('')
    const [price, setPrice] = useState('')

    const handleAddMedicine = () => {
        if (name === '') {
            alert('Vui lng nhp tn thuc!')
            return
        }

        if (price === '') {
            alert('Vui lng nhp gi thuc!')
            return
        }

        const db = getDatabase()

        const databaseRef = ref(db, 'danhSachThuoc')
        const newRef = push(databaseRef)

        const timestamp = new Date().getTime()

        const data = {
            id: newRef.key,
            tenThuoc: name,
            gia: {
                [timestamp]: Number(price)
            },
            thoiGianTao: timestamp
        }

        set(newRef, data)
            .then(() => alert('Thm thuc mi thnh cng!'))
            .catch((e) => console.log(e))

        setName('')
        setPrice('')
    }

    return (
        <Col>
            <p>Thm thuc mi</p>
            <Input value={name} placeholder='Tn thuc' onChange={(e) => setName(e.target.value)} />
            <Input
                value={price}
                placeholder='Gi thuc'
                onChange={(e) => setPrice(e.target.value)}
            />
            <Button onClick={handleAddMedicine} style={{ color: 'red ' }}>
                Thm
            </Button>
        </Col>
    )
}

export default AddMedicineView
